.message {
    display: flex;
    justify-content: flex-end;
    margin: 24px 118px 24px 0;
    position: relative;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse

}

.imageAndText img {
    margin-left: 20px;
    width: 48px;
    height: 48px;
    flex-direction: row-reverse
}

.text {
    background-color: #0066CC;
    min-width: 527px;
    min-height: 60px;
    color: white;
    border-radius: 11px;
    position: relative;
}

.text:after {
    position:absolute;
    content:"";
    right: -10px;
    bottom: 15px;
    transform:  scaleY(0.5) rotate(45deg);
    width:30px;
    height:30px;
    background:#0066CC;
}

.name {
    margin: 7px 25px 0 476px;
}

.messageText {
    font-weight: 400;
    font-size: 16px;
    margin: 3px 25px 8px 13px;
}

.time {
position: absolute;
bottom: -5px;
    right: 11px;
    font-weight: bold;
    font-size: 10px;
}