.friendMessage {
    display: flex;
    margin: 24px 0 24px 118px;
    position: relative;
}

.friendImageAndText {
    display: flex;

}

.friendImageAndText img {
    margin-right: 20px;
    width: 48px;
    height: 48px;
}

.friendText {
    background-color: #F5F7FB;
    min-width: 527px;
    min-height: 60px;
    color: black;
    border-radius: 11px;
    position: relative;
}

.friendText:after {
    position:absolute;
    content:"";
    left: -10px;
    bottom: 15px;
    transform:  scaleY(0.5) rotate(45deg);
    width:30px;
    height:30px;
    background:#F5F7FB;
}

.friendName {
    margin: 7px 476px 0 25px;
}

.friendMessageText {
    font-weight: 400;
    font-size: 16px;
    margin: 3px 13px 8px 25px;
}

.friendTime {
    position: absolute;
    bottom: -5px;
    left: 11px;
    font-weight: bold;
    font-size: 10px;
}
